import { FdCommon } from './FdCommon';

/**
 * PMTS
 * 
 * PMTSPModel
 * PMTSPMTS(Layer)MatrixSet
 * MatrixSetMatrixMatrixPModel
 * 
 * 
 * ```
 * // jsonPMTSCapabilitiesPMTSjson(Layer)
 * jsonPMTSCapabilities.contents.layers.forEach(layer => {
 *     // MatrixSet
 *     let matrixSets = Freedo.FdServer.FdPMTSParser.getMatrixSets(jsonPMTSCapabilities, layer);
 *     matrixSets.forEach(matrixSet => {
 *         // MatrixSetMatrix
 *         matrixSet.matrix.forEach(matrix => {
 *             // MatrixPModelURL
 *             let pModelURL = Freedo.FdServer.FdPMTSParser.getPModelURL(layer,
 *                 matrixSet, matrix, token);
 * 
 *             // pModelURLPModel
 *             var pModel = viewer.scene.primitives.add(new Freedo.FreedoPModelset({
 *                 url: pModelURL
 *             }));
 * 
 *             // pModelMatrix
 *             pModelInfos.push({
 *                 pModel,
 *                 matrix,
 *                 matrixSet,
 *                 layer
 *             });
 * 
 *             // PModelPModel
 *             pModel.readyPromise.then(function (pModel) {
 *                 viewer.camera.viewBoundingSphere(pModel.boundingSphere,
 *                     new Freedo.HeadingPitchRange(
 *                         0, -0.5, 0));
 *             });
 *         })
 * 
 *         // matrixSet.uidMatrixSetID
 *         // ID
 *         // IDID
 *         Freedo.FdServer.FdPMTSParser.getUidJson(layer, matrixSet, matrix, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------uid-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 * 
 *         // ID
 *         // 
 *         Freedo.FdServer.FdPMTSParser.getCidJson(layer, matrixSet, matrix, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------cid-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 * 
 *         // ID
 *         // BoundsMinBoundsMax
 *         Freedo.FdServer.FdPMTSParser.getPropJson(layer, matrixSet, matrix, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------prop-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 *     })
 * })
 * ```
 * 
 * @alias FdServer.FdPMTSParser
 * @namespace
 */
class FdPMTSParser {
    constructor() {
        throw new Error('FdPMTSParser should not be instantiated!');
    }
}

// file uid cid prop
let _getResourceTemplateURL = function(layer, matrixSet, resourceType) {
    if (layer.resourceurl) {
        return layer.resourceurl.find((element) => {
            return element.type === resourceType;
        }).template;
    }
    if (matrixSet.resourceurl) {
        return matrixSet.resourceurl.find((element) => {
            return element.type === resourceType;
        }).template;
    }
}

function _getUidQueryStr(layer, matrixSet, matrix, UID, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'uid');
    template = template.replace(/{matrixset}/g, matrixSetIdentifier);
    template = template.replace(/{matrix}/g, matrixIdentifier);
    template = template.replace(/{uid}/g, UID);
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}

function _getCidQueryStr(layer, matrixSet, matrix, tileno, CID, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'cid');
    if (!matrixSet.propstoragetype || matrixSet.propstoragetype === 1) {
        template = template.replace(/{matrixset}/g, matrixSetIdentifier);
        template = template.replace(/{matrix}/g, matrixIdentifier);
        template = template.replace(/{cid}/g, CID);
    }
    if (matrixSet.propstoragetype === 2) {
        template = template.replace(/{tileno}/g, tileno);
        template = template.replace(/{pguid}/g, CID);
    }
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}

function _getPropQueryStr(layer, matrixSet, matrix, prop, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'prop');
    if (!matrixSet.propstoragetype || matrixSet.propstoragetype === 1) {
        template = template.replace(/{matrixset}/g, matrixSetIdentifier);
        template = template.replace(/{matrix}/g, matrixIdentifier);
        template = template.replace(/{comid}/g, prop);
    }
    if (matrixSet.propstoragetype === 2) {
        template = template.replace(/{guid}/g, prop);
    }
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}

function _getTagQueryStr(layer, matrixSet, matrix, tag, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'tag');
    template = template.replace(/{matrixset}/g, matrixSetIdentifier);
    template = template.replace(/{matrix}/g, matrixIdentifier);
    template = template.replace(/{tag}/g, tag);
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}


/**
 * IDLayer
 * @param {Object} jsonPMTSCapabilities PMTSJSON
 * @param {String} layerIdentifier LayerID
 * @returns {Object} Layer
 */
FdPMTSParser.getLayerFromID = function(jsonPMTSCapabilities, layerIdentifier) {
    return jsonPMTSCapabilities.contents.layers.find((element) => {
        return element.identifier === layerIdentifier;
    })
}

/**
 * IDMatrixSet
 * @param {Object} jsonPMTSCapabilities PMTSJSON
 * @param {String} matrixSetIdentifier MatrixSetID
 * @returns {Object} MatrixSet
 */
FdPMTSParser.getMatrixSetFromID = function(jsonPMTSCapabilities, matrixSetIdentifier) {
    return jsonPMTSCapabilities.contents.matrixsets.find((element) => {
        return element.identifier === matrixSetIdentifier;
    })
}

/**
 * IDMatrix
 * @param {Object} matrixSet MatrixSetJSON
 * @param {String} matrixIdentifier MatrixID
 * @returns {Object} Matrix
 */
FdPMTSParser.getMatrixFromID = function(matrixSet, matrixIdentifier) {
    return matrixsets.find((element) => {
        return element.identifier === matrixIdentifier;
    })
}

/**
 * LayerMatrixSet
 * @param {Object} jsonPMTSCapabilities PMTSJSON
 * @param {Object} layer Layer
 * @returns {Object[]} MatrixSet 
 */
FdPMTSParser.getMatrixSets = function(jsonPMTSCapabilities, layer) {
    let matrixSets = [];
    layer.matrixsetlink.forEach((matrixsetlink) => {
        let matrixSet = FdPMTSParser.getMatrixSetFromID(jsonPMTSCapabilities, matrixsetlink);
        if (typeof matrixSet === 'undefined') {
            throw new Error('');
        }

        matrixSets.push(matrixSet);
    });

    return matrixSets;
}

/**
 * PModelURL
 * @param {Object} layer Layer
 * @param {Object} matrixSet MatrixSet
 * @param {Object} matrix Matrix
 * @param {String} token tokentokenundefined
 * @returns {String} PModelURL
 */
FdPMTSParser.getPModelURL = function(layer, matrixSet, matrix, token) {
    let matrixSetIdentifier = matrixSet.identifier;
    let matrixIdentifier = matrix.identifier;
    let template = _getResourceTemplateURL(layer, matrixSet, 'file');
    template = template.replace(/{matrixset}/g, matrixSetIdentifier);
    template = template.replace(/{matrix}/g, matrixIdentifier);
    template = template.replace(/{filepath}/g, matrixIdentifier);
    if (typeof token !== 'undefined') {
        template = template + `?token=${token}`;
    }

    return template;
}

/**
 * 
 * @param {Object} jsonPMTSCapabilities
 * @private 
 */
function printJsonPMTSCapabilitiesInfo(jsonPMTSCapabilities, token) {
    let pModelInfos = [];
    jsonPMTSCapabilities.contents.layers.forEach(layer => {
        let matrixSets = Freedo.FdServer.FdPMTSParser.getMatrixSets(jsonPMTSCapabilities, layer);
        matrixSets.forEach(matrixSet => {
            matrixSet.matrix.forEach(matrix => {
                let pModelURL = Freedo.FdServer.FdPMTSParser.getPModelURL(layer,
                    matrixSet, matrix, token);
                pModelInfos.push({
                    pModelURL: pModelURL,
                    matrix: matrix,
                    matrixSet: matrixSet,
                    layer: layer
                });
            })
        })
    })
    return pModelInfos;
}

/**
 * PMTS
 * @param {String} url PMTStokenaccesskey
 * @param {FdServer.FdPMTSParser~Callback} callback 
 */
FdPMTSParser.getPModelURLArray = async function(url, callback) {
    let queryString = url.split('?')[1];
    var endUserOptions = Freedo.queryToObject(queryString);
    if (endUserOptions.accesskey) {
        Freedo.FdServer.FdShareServer.getLayerAccesskeyResource(url, 'json', (jsonPMTSCapabilities, error) => {
            if (error) {
                console.error('FdServer.FdShareServer.getLayerAccesskeyResource got error: ' + error);
                return;
            }
            let result = printJsonPMTSCapabilitiesInfo(jsonPMTSCapabilities);
            callback(result);
        });
    } else if (endUserOptions.token) {
        Freedo.FdServer.FdPMTSServer.getResource(url.split('?')[0], endUserOptions.token, (jsonPMTSCapabilities, error) => {
            if (error) {
                console.error('FdPMTSServer.getJson got error: ' + error);
                return;
            }
            let result = printJsonPMTSCapabilitiesInfo(jsonPMTSCapabilities, endUserOptions.token);
            callback(result);
        });
    } else {
        callback([])
    }
}


async function _getJson(url, callback) {
    try {
        let response = await FdCommon.ajaxJson({
            url: url,
            type: 'GET'
        })
        callback(response);
    } catch (error) {
        callback(undefined, error);
    }
}

/**
 * 
 * @param {Object} layer Layer
 * @param {Object} matrixSet MatrixSet
 * @param {Object} matrix Matrix
 * @param {String} uid ID
 * @param {String} token tokentokenundefined
 * @param {FdServer.FdPMTSParser~Callback} callback 
 * 
 * @example
 *         Freedo.FdServer.FdPMTSParser.getUidJson(layer, matrixSet, matrixSet.uid,
 *             token,
 *             (json, error) => {
 *                 console.log('--------------uid-------------');
 *                 if (typeof error === 'undefined') {
 *                     console.log(json);
 *                 } else {
 *                     console.log('error: ' + error);
 *                 }
 *                 console.log(' ');
 *             });
 */
FdPMTSParser.getUidJson = function(layer, matrixSet, matrix, uid, token, callback) {
    let uidQueryStr = _getUidQueryStr(layer, matrixSet, matrix, uid, token);
    // Freedo.FdServer.FdPMTSServer.getJson(uidQueryStr, (json, error) => {
    //     callback(json, error);
    // });
    _getJson(uidQueryStr, (json, error) => {
        callback(json, error);
    });
}

