import { FdCommon } from './FdCommon';

/**
 * PMTS
 * @namespace
 * @alias FdServer.FdPMTSServer
 */
class FdPMTSServer {}


/**
 * 
 * @param {Object} data  
 * @param {String} keyword 
 * @param {String} servicetype PMTS
 * @param {String} baseURL 
 * @param {String} token token
 * @param {FdServer.FdPMTSServer~FdGetResourceListCallback} response 
 */
FdPMTSServer.getResourceCount = async function(data, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/freeserver-admin/admin/layer/count?token=${token}`;
    let response = [];
    let err;
    if (!data) {
        data = {};
        data.servicetype = 'PMTS';
    }
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        err = error;
        console.log('');
    }
    callback(response, err);
}

/**
 * 
 * @param {Object} data  
 * @param {String} keyword 
 * @param {Number} offset 
 * @param {Number} pagecount 
 * @param {String} servicetype PMTS
 * @param {String} baseURL 
 * @param {String} token token
 * @param {FdServer.FdPMTSServer~FdGetResourceListCallback} response 
 */
FdPMTSServer.getResourceList = async function(data, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/freeserver-admin/admin/layer/get?token=${token}`;
    let response = [];
    let err;
    if (!data) {
        data = {};
        data.servicetype = 'PMTS';
    }
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        err = error;
        console.log('');
    }
    callback(response, err);
}

/**
 * FdPMTSServer
 * @callback FdServer.FdPMTSServer~FdGetResourceListCallback
 * @param {Object} response 
 * @param {String} error errorundefined
 */

/**
 * 
 * @param {String} capaurl ResourceListPMTScapaurl
 * @param {String} token token
 * @param {FdServer.FdPMTSServer~FdGetResourceCallback} callback 
 */
FdPMTSServer.getResource = async function(capaurl, token, callback) {
    let pmtsUrl = capaurl;
    let response = undefined;
    if (token) {
        pmtsUrl = capaurl + '?token=' + token
    }
    try {
        response = await FdCommon.ajaxJson({
            url: pmtsUrl,
            type: 'GET'
        })
    } catch (error) {
        console.error('');
    }
    callback(response);
}

/**
 * FdPMTSServer
 * @callback FdServer.FdPMTSServer~FdGetResourceCallback
 * @param {Object} response 
 * @param {String} error errorundefined
 */

export {
    FdPMTSServer
}