/**
 * 
 * @namespace
 * @alias FdMisc.FdMath
 */
class FdMath {
    constructor() {

    }
}

/**
 * 
 * @param {Object} v 
 * @returns {Boolean} true
 */
FdMath.isNumber = function(v) {
    return typeof v === 'number' && isFinite(v);
}

/**
 * 
 * @param {Number} numberRound 
 * @param {Number} roundDigit 
 * @returns {Number} 
 */
FdMath.round = function(numberRound, roundDigit) {
    if(numberRound >= 0) {
        var tempNumber = parseInt((numberRound * Math.pow(10, roundDigit) + 0.5)) / Math.pow(10, roundDigit);
        return tempNumber;
    } else {
        var numberRound1 = -numberRound;
        var tempNumber = parseInt((numberRound1 * Math.pow(10, roundDigit) + 0.5)) / Math.pow(10, roundDigit);
        return -tempNumber;
    }
}

export {
    FdMath
};