/**
 * 
 *
 * @alias FdCamera.aerialView
 * @param {Freedo.Scene} scene 
 * @param {Number} [duration] 
 * @param {Number} [west] [-180.0, 180.0])80
 * @param {Number} [south] [-90.0, 90.0])5
 * @param {Number} [east] [-180.0, 180.0])135
 * @param {Number} [north] [-90.0, 90.0])55
 *
 * @example
 * var scene = viewer.scene;
 * Freedo.FdCamera.aerialView(scene);//5
 * //Freedo.FdCamera.aerialView(scene ,2 ,80,5,135,55); //
 */
function aerialView(scene, duration, west, south, east, north) {
    if (!(scene instanceof Freedo.Scene)) {
        throw new Freedo.DeveloperError('The parameter is not an instance of Freedo.Scene.');
    }
    var camera = scene.camera;

    //duration = Freedo.defaultValue(duration ,80) ;
    west = Freedo.defaultValue(west, 80);
    south = Freedo.defaultValue(south, 5);
    east = Freedo.defaultValue(east, 135);
    north = Freedo.defaultValue(north, 55);

    var aerialViewRectangle = Freedo.Rectangle.fromDegrees(west, south, east, north); // HOME

    //Freedo.Camera.flyHomeCamera.DEFAULT_VIEW_RECTANGLEaerialViewRectangle
    var mode = camera._mode;

    if (mode === Freedo.SceneMode.MORPHING) {
        camera._scene.completeMorph();
    }

    if (mode === Freedo.SceneMode.SCENE2D) {
        camera.flyTo({
            destination: aerialViewRectangle,
            duration: duration,
            endTransform: Freedo.Matrix4.IDENTITY
        });
    } else if (mode === Freedo.SceneMode.SCENE3D) {
        var destination = camera.getRectangleCameraCoordinates(aerialViewRectangle);

        var mag = Freedo.Cartesian3.magnitude(destination);
        mag += mag * Freedo.Camera.DEFAULT_VIEW_FACTOR;
        Freedo.Cartesian3.normalize(destination, destination);
        Freedo.Cartesian3.multiplyByScalar(destination, mag, destination);

        camera.flyTo({
            destination: destination,
            duration: duration,
            endTransform: Freedo.Matrix4.IDENTITY
        });
    } else if (mode === Freedo.SceneMode.COLUMBUS_VIEW) {
        var maxRadii = camera._projection.ellipsoid.maximumRadius;
        var position = new Freedo.Cartesian3(0.0, -1.0, 1.0);
        position = Freedo.Cartesian3.multiplyByScalar(Freedo.Cartesian3.normalize(position, position), 5.0 * maxRadii, position);
        camera.flyTo({
            destination: position,
            duration: duration,
            orientation: {
                heading: 0.0,
                pitch: -Freedo.Math.acos(Freedo.Cartesian3.normalize(position, pitchScratch).z),
                roll: 0.0
            },
            endTransform: Freedo.Matrix4.IDENTITY,
            convert: false
        });
    }
}

export { aerialView };