import {
    FdAnalysisLineDistance
} from './Analysis/FdAnalysisLineDistance';

import {
    FdAnalysisPoint
} from './Analysis/FdAnalysisPoint';

import {
    FdAnalysisArea
} from './Analysis/FdAnalysisArea';

import {
    FdAnalysisHeight
} from './Analysis/FdAnalysisHeight';
/**
 * 
 * @alias FdMicroApp.FdAnalysisManager
 * @class
 * 
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * var analysisManager = new Freedo.FdMicroApp.FdAnalysisManager(viewer);
 * analysisManager.start('LINE_DISTANCEs');
 */
class FdAnalysisManager {
    constructor(viewer) {
        this._viewer = viewer;
        this._survey = undefined;
        this._depthTestAgainstTerrain = true;
        this._mode = '';

        this._event = new FreeDo.Event();
        this._eventHelper = new FreeDo.EventHelper();

        this._surveyLine = new FdAnalysisLineDistance(this._viewer);
        this._surveyPoint = new FdAnalysisPoint(this._viewer);
        this._surveyArea = new FdAnalysisArea(this._viewer);
        this._surveyHeight = new FdAnalysisHeight(this._viewer);
        
        this._lsnr = undefined;
    }

    get surveyOptions() {
        return this.getData();
    }

    set surveyOptions(v) {
        if(!Freedo.defined(v)) {
            return;
        }
        this._surveyLine.setData(v);
        this._surveyPoint.setData(v.point);
        this._surveyArea.setData(v.area);
        this._surveyHeight.setData(v.height);
    }
}

/**
 * 
 * @param {String} mode 'LINE_DISTANCE''SEGMENTS_DISTANCE''SINGLE_POINT''AREA''HEIGHT'
 */
FdAnalysisManager.prototype.start = function(mode) {
    this.setMode(mode);
}

/**
 * 
 * @private
 */
FdAnalysisManager.prototype.setMode = function(mode) {
    if(mode == 'CANCEL') {
        this.reset();
        return;
    }

    if(mode !== 'LINE_DISTANCE' &&
        mode !== 'SEGMENTS_DISTANCE' &&
        mode !== 'SINGLE_POINT' &&
        mode !== 'AREA' &&
        mode !== 'HEIGHT') {
        console.warn('' + mode);
        return;
    }

    this.reset();

    this._mode = mode;

    if(mode === 'LINE_DISTANCE' || mode === 'SEGMENTS_DISTANCE') {
        this._survey = this._surveyLine;
    }

    if(mode === 'SINGLE_POINT') {
        this._survey = this._surveyPoint;
    }

    if(mode === 'AREA') {
        this._survey = this._surveyArea;
    }
    
    if(mode === 'HEIGHT') {
        this._survey = this._surveyHeight;
    }

    var that = this;
    this._lsnr = this._survey.on(function(eventType, param) {
        if(eventType == 'Point') {
            that._event.raiseEvent('Point', param);
        }
        
        if(eventType == 'HeightPoint') {
            that._event.raiseEvent('Point', param);
        }

        if(eventType == 'DataChanged') {
            that._event.raiseEvent(eventType, param);
        }
    });

    this._survey.run(this._mode);

    this._event.raiseEvent('Start', {
        mode: this._mode
    });
}

/**
 * 
 * @returns {String} 'LINE_DISTANCE''SEGMENTS_DISTANCE''SINGLE_POINT''AREA''HEIGHT'
 */
FdAnalysisManager.prototype.getMode = function() {
    return this._mode;
}

/**
 * 
 * @param {Boolean} singleMode 
*/
FdAnalysisManager.prototype.setSingleMode = function(singleMode) {
    if(this._mode == 'SINGLE_POINT'){
        this._survey.setSingleMode(singleMode);
    }
}

/**
 * 
 * @returns {Object} 
 * {
 *  line:[{
 *     pointsG:[[,,],[,,]],
 *     pointsC:[,],
 *     length:,
 *     vLength:,
 *     hLength:,
 *  },...],
 *  polyline:[{
 *     lines:[{
 *         pointsG:[[,,],[,,]],
 *         pointsC:[,],
 *         length:,
 *         vLength:,
 *         hLength:,
 *     },...],
 *     sumLenght:
 *  },...],
 *  point:[{
       pointG:[,,],
 *     pointC:
 *  },...],
 *  height:[{
       pointG:[,,],
 *     pointC:
 *  },...],
 *  area:[{
 *     pointsG: [[,],[,],[,],...],
 *     pointsC: [,,,...],
 *     area: 
 *  },...]
 * }
 */
FdAnalysisManager.prototype.getData = function() {
    var dl = this._surveyLine.getData();
    var dp = this._surveyPoint.getData();
    var da = this._surveyArea.getData();
    var dh = this._surveyHeight.getData();
    //console.log(dp.point);
    return {
        line: dl.line,
        polyline: dl.polyline,
        point: dp.point,
        area: da.area,
        height: dh.height
    };
}

/**
 * 
 */
FdAnalysisManager.prototype.cancel = function() {
    this.reset();
    this._event.raiseEvent('Cancel', {
        mode: this._mode
    });
}

/**
 * 
 * @param {FdMicroApp.FdAnalysisManager~FdAnalysisManagerCallback} listener 
 * @param {Object} [scope] listener
 * @returns {Freedo.Event~RemoveCallback} 
 */
FdAnalysisManager.prototype.on = function(listener, scope) {
    return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdMicroApp.FdAnalysisManager~FdAnalysisManagerCallback
 * @param {String} eventType StartCancelDataChangedPointHeightPoint
 * @param {Object} eventArg Start/CancelDataChangedPoint
 */

/**
 * 
 */
FdAnalysisManager.prototype.reset = function() {
    if(this._survey)
        this._survey.reset();
    if(this._lsnr)
        this._lsnr();
}

/**
 * 
 */
FdAnalysisManager.prototype.removeAll = function() {
    this._surveyLine.removeAll();
    this._surveyPoint.removeAll();
    this._surveyArea.removeAll();
    this._surveyHeight.removeAll();
    this._event.raiseEvent('DataChanged', {});
    this._viewer.scene.requestRender();
}

/**
 * 
 */
FdAnalysisManager.prototype.dispose = function() {
    this.reset();
}

export {
    FdAnalysisManager
};