/**
 * 
 * 30
 * 
 * @alias FdTools.FdKeyboard
 * @class
 */
class FdKeyboard {
	constructor(viewer) {
		this._viewer = viewer;
		//this._camera = project.getCamera();

		this._event = new FreeDo.Event();
		this._eventHelper = new FreeDo.EventHelper();

		this._divID = this._viewer._container.id;
		this._bActive = false;
		this._bRun = false;
		this._windowTimer = null;
		this._metaData = [];

		//this._camera.setKBRef(this);
	}
}

/**
 * 
 * timer
 */
FdKeyboard.prototype.enable = function() {
	this._bActive = true;
	this.setFocus();
	this.addEventLsnr();
	this._event.raiseEvent('StatusChanged', {
		status: this._bActive
	});
}

/**
 * 
 */
FdKeyboard.prototype.disable = function() {
	this._bActive = false;
	this._event.raiseEvent('StatusChanged', {
		status: this._bActive
	});
}

/**
 * 
 * @return {Boolean} true
 */
FdKeyboard.prototype.getStatus = function() {
	return this._bActive;
}

/**
 * 
 * @param {Boolean} status true
 */
FdKeyboard.prototype.setStatus = function(status) {
	this._bActive = status;
}

/**
 * focus
 * @private
 */
FdKeyboard.prototype.setFocus = function() {
	var ele = document.getElementById(this._divID);
	ele.setAttribute('tabindex', 1);
	ele.focus();
}

FdKeyboard.prototype.prevent = function(e) {
	var keyCode = e.keyCode || e.which;
	//    W A S D
	if(keyCode == 38 || keyCode == 40 || keyCode == 37 || keyCode == 39 
	    || keyCode == 87 || keyCode == 65 || keyCode == 83 || keyCode == 68) {
		e.stopPropagation();
		e.preventDefault();
	}
}

FdKeyboard.prototype.onKeyDown = function(e) {
	if(!this._bActive)
		return;
	e = window.event || e;
	var keyCode = e.keyCode || e.which;
	this._metaData[e.keyCode] = true;
	this.startTimer();
	this.prevent(e);
}

FdKeyboard.prototype.onKeyUp = function(e) {
	if(!this._bActive)
		return;
	e = window.event || e;
	var keyCode = e.keyCode || e.which;
	this._metaData[e.keyCode] = false;
	this.stopTimer();
	this.prevent(e);
}

FdKeyboard.prototype.startTimer = function(e) {
	var that = this;
	if(this._windowTimer == null) {
		this._windowTimer = window.setInterval(function() {
			that._event.raiseEvent('timer', {
				metaData: that._metaData
			});
			that._event.raiseEvent('KeyPressed', {
				metaData: that._metaData
			});
		}, 33);
	}
}

FdKeyboard.prototype.stopTimer = function(e) {
	if(this._windowTimer != null) {
		var clear = true;
		for(var key in this._metaData) {
			if(this._metaData[key]) {
				clear = false;
				break;
			}
		}
		if(clear) {
			window.clearInterval(this._windowTimer);
			this._windowTimer = null;
		}
	}
}

FdKeyboard.prototype.addEventLsnr = function() {
	var that = this;
	if(!this._bRun) {
		this._bRun = true;
		let canvas = this._viewer.canvas;
		canvas.addEventListener('click', function(e) {
			if(e.target === canvas) {
				this.setAttribute('tabindex', 1);
				this.focus();
			}
		});
		canvas.onkeydown = function(e) {
			if(e.target === canvas) {
				that.onKeyDown(e);
			}
		}
		canvas.onkeyup = function(e) {
			if(e.target === canvas) {
				that.onKeyUp(e);
			}
		}
	}
}
/**
 * 
 * @param {FdTools.FdKeyboard~FdKeyboardCallback} listener KeyPressed,StatusChanged
 * @param {Object} [scope] listener
 * @returns {Freedo.Event~RemoveCallback} 
 */
FdKeyboard.prototype.on = function(listener, scope) {
	return this._eventHelper.add(this._event, listener, scope);
}

/**
 * @callback FdTools.FdKeyboard~FdKeyboardCallback
 * @param {String} eventType KeyPressed/StatusChanged
 * @param {Object} eventArg KeyPressedStatusChanged
 */

/**
 * 
 */
FdKeyboard.prototype.reset = function() {
	this._bActive = false;
}

/**
 * 
 */
FdKeyboard.prototype.dispose = function() {
	this.reset();
}

export {
	FdKeyboard
};