import { FdCommon } from './FdCommon';

/**
 * PMTS
 * @namespace
 * @alias FdServer.FdPMTSServer
 * @private
 */
class FdModelServer {
    constructor() {
        throw new Error('FdModelServer should not be instantiated!');
    }
}

/**
 * 
 * @param {Object} data 
 * @param {String} data.keyword 
 * @param {String} baseURL 
 * @param {String} token token
 * @param {FdServer.FdModelServer~ResponseCallback} response 
 * @private
 */
FdModelServer.getResourceFinishCount = async function(data, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/freeserver-one/services/one/resource/finish/count?token=${token}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            type: 'POST'
        })
    } catch (error) {
        console.error('', error);
    }
    callback(response);
}

