var scrachLastTransform = new Freedo.Matrix4();

/**
 * 
 *
 * @alias FdCamera.flyAround
 * @param {Freedo.Cartesian3} position 
 * @param {Freedo.Scene} scene 
 * @param {Object} [options] 
 * @param {Number} [options.duration] 3s
 * @param {Number} [options.rotateAngleRange]  Freedo.Math.PI * 4.0
 * @param {Freedo.EasingFunction} [options.easingFunction] Freedo.EasingFunction Freedo.EasingFunction.QUADRACTIC_IN_OUT
 * @param {Number} [options.delay] 0s
 * @returns {Freedo.Event~RemoveCallback} 
 * 
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * Freedo.FdCamera.flyAround(Freedo.Cartesian3.fromDegrees(116, 31, 100), viewer.scene, {
 *     delay: 5
 * });
 */
function flyAround(position, scene, options) {
    var camera = scene.camera;

    if (!Freedo.defined(options)) {
        options = {};
    }

    var duration = Freedo.defaultValue(options.duration, 3);
    var rotateAngleRange = Freedo.defaultValue(options.rotateAngleRange, Freedo.Math.PI * 4.0);
    var easingFunction = Freedo.defaultValue(options.easingFunction, Freedo.EasingFunction.QUADRACTIC_IN_OUT);
    var delay = Freedo.defaultValue(options.delay, 0.0);

    var oldTransform = Freedo.Matrix4.clone(camera.transform, scrachLastTransform);
    var transform = Freedo.Transforms.eastNorthUpToFixedFrame(position);
    camera.lookAtTransform(transform);

    var tween = scene.tweens.add({
        duration: duration,
        easingFunction: easingFunction,
        startObject: {
            angle: 0.0
        },
        stopObject: {
            angle: rotateAngleRange
        },
        delay: delay,
        update: (function() {
            var lastAngle = 0.0;
            return function(val) {
                //console.log(JSON.stringify(val));
                var diff = val.angle - lastAngle;
                //console.log('diff: ' + diff);
                if (diff > 0) {
                    camera.rotate(Freedo.Cartesian3.UNIT_Z, diff);
                }
                lastAngle = val.angle;
            };
        })(),
        complete: function() {
            //console.log('complete');
            tween.cancelTween();
        },
        cancel: function() {
            //console.log('cancel');
            camera.lookAtTransform(oldTransform);
        }
    });

    return function() {
        tween.cancelTween();
    };
}

export { flyAround };