const uiHtml =
    `
    <div class="FD-login-con">
            <div class="FD-login-title">
                <span></span>
            </div>
            <div class="FD-input-area">
                <input type="text" id="lg_tel" placeholder="">
                <span id="lg_tel_error"></span>
                <input type="password" id="lg_pw" placeholder="">
                <span id="lg_pw_error"></span>
            </div>
            <button id="FD-user-logining-btn"></button>
    </div>
`;


/**
 * 
 * @alias FdWidgets.FdUserLogining
 * @param {Freedo.Viewer} viewer Freedo
 * @param {Element} container 
 */
class FdUserLogining {
    constructor(viewer, container) {
        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._token = undefined;

        this._viewer = viewer;
        this._container = container;
        container.innerHTML = uiHtml;

        let baseURL = '//' + window.location.host;

        var that = this;

        // 
        function login() {
            let lg_tel = document.getElementById('lg_tel');
            let lg_pw = document.getElementById('lg_pw');
            let lg_tel_error = document.getElementById('lg_tel_error');
            let lg_pw_error = document.getElementById('lg_pw_error');
            lg_tel_error.innerHTML = '';
            lg_pw_error.innerHTML = '';
            if (!lg_tel.value) {
                lg_tel_error.innerHTML = '';
                return false;
            }
            if (!lg_pw.value) {
                lg_pw_error.innerHTML = '';
                return false;
            }

            let data = {
                loginNum: lg_tel.value,
                password: lg_pw.value
            }

            this._token = undefined;
            Freedo.FdServer.FdUserServer.userLogin(data, baseURL, res => {
                if (res.code === 'PU03000') {
                    lg_pw_error.innerHTML = res.msg;
                    return false;
                }
                if (res.code === 'PU03010') {
                    lg_tel_error.innerHTML = res.msg;
                    return false;
                }
                console.log('token: ' + res.token);
                that._token = res.token;
                container.style.display = 'none';
                that._event.raiseEvent('TokenChanged', res.token);
            });
        }

        let loginingButton = document.getElementById('FD-user-logining-btn');
        loginingButton.onclick = login;

        function register() {

        }
    }

    /**
     * token
     * @returns {String} tokentokenundefined
     */
    getToken() {
        return this._token;
    }

    /**
     * FdUserLogining
     * @param {FdTools.FdUserLogining~FdTokenChangedCallback} listener TokenChanged
     * @param {Object} [scope] listener
     * @returns {Freedo.Event~RemoveCallback} 
     * 
     * @example
     * 
     * userLogining.on(function(eventType, token) {
     *    if (eventType === 'TokenChanged') {
     *        writeToLocalStorage(token);
     *    }
     * });
     *
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdTools.FdUserLogining~FdTokenChangedCallback
     * @param {String} eventType TokenChanged
     * @param {String} token
     */

    /**
     * 
     */
    dispose() {
        this._container.innerHTML = '';
        this._viewer = undefined;
        this._container = undefined;

        this._eventHelper.removeAll();
        this._eventHelper = undefined;
        this._event = undefined;
    }
}

export {
    FdUserLogining
};