/**
 * 
 *
 * @alias FdCamera.FdTrackManager
 * @param {Freedo.Scene} scene 
 * @class
 * 
 * @example
 * var container = document.getElementById('freedoContainer');
 * 
 * var trackManager = new Freedo.FdTrackManager(viewer.scene);
 * 
 * //...
 * 
 * trackManager.trackByBoundingSphere(boundingSphere);
 */
class FdTrackManager {
    constructor(scene) {
        this._scene = scene;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();
    }

    /**
     * 
     * @param {Freedo.BoundingSphere} boundingSphere 
     * 
     * @example
     * 
     * trackManager.trackByBoundingSphere(tileset.boundingSphere);
     */
    trackByBoundingSphere(boundingSphere) {
        this._scene.screenSpaceCameraController.bimTrackByBoundingSphere(boundingSphere);
        this._event.raiseEvent('beginTrack', boundingSphere);
    }

    /**
     * 
     * 
     * @example
     * 
     * trackManager.cancelTrack();
     */
    cancelTrack() {
        this._scene.screenSpaceCameraController.bimUnTrack();
        this._event.raiseEvent('cancelTrack');
    }

    /**
     * 
     * @returns {Boolean} true:false
     * 
     * @example
     * 
     * var bTracking = trackManager.isTracking() ;
     * 
     */
    isTracking() {
        return this._scene.screenSpaceCameraController.isTracking();
    }

    /**
     * FdTrackManager
     * @param {FdCamera.FdTrackManager~FdTrackManagerCallback} listener FdTrackManagerbeginTrack,cancelTrack
     * @param {Object} [scope] listener
     * @returns {Freedo.Event~RemoveCallback} 
     *
     * 
     * @example
     * 
     * trackManager.on( function(eventType, param) {
     *     if(eventType === 'beginTrack'){
     *        var boundingSphere = param ;
     *        //do something
     *     } else if (eventType === 'cancelTrack') {
     *        //do something
     *     }
     * })
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdCamera.FdTrackManager~FdTrackManagerCallback
     * @param {String} eventType beginTrack,cancelTrack
     * @param {Freedo.BoundingSphere | undefined} eventArg beginTrack,cancelTrack
     */

    /**
     * 
     * 
     */
    dispose() {
        //this.reset();
        this._scene.screenSpaceCameraController.bimUnTrack();
        this._eventHelper.removeAll();
    }
}

export {
    FdTrackManager
};