import { FdCommon } from './FdCommon';

/**
 * 
 * @namespace
 * @alias FdServer.FdShareServer
 */
class FdShareServer {}

/**
 * ObjectId
 * FdShareServer.addShareObjectID
 * @param {Object} params 
 * @param {String} params.id id
 * @param {String} params.name name
 * @param {String} baseURL 
 * @param {String} token token
 * @param {FdServer.FdShareServer~ResponseCallback} response 
 * @private
 */
FdShareServer.getShareObjectID = async function(params, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/cloudlink-core-framework/app/dataresource/queryList?token=${token}&appId=0154981b-11c5-49ad-839d-a3420af507d8&dataId=${params.id}&dataName=${params.name}`;
    let response = [];
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            type: 'GET'
        })
    } catch (error) {
        console.log(error);
    }
    callback(response);
}

/**
 * objectId
 * @param {Object} params 
 * @param {String} params.id id
 * @param {String} params.name name
 * @param {String} baseURL 
 * @param {String} token token
 * @param {FdServer.FdShareServer~ResponseCallback} response 
 * @private
 */
FdShareServer.addShareObjectID = async function(params, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/cloudlink-core-framework/app/dataresource/add?token=${token}`;
    let timeline = new Date().getTime();
    let response = [];
    let data = {
        'appId': '0154981b-11c5-49ad-839d-a3420af507d8',
        'resourceCode': `freedo_data_lid_${timeline}`,
        'resourceName': `freedo_data_lname_${timeline}`,
        'parentId': '610be183-1dda-43ed-8737-49fa2a55cc80',
        'dataId': params.id,
        'dataName': params.name
    }
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.log(error);
    }
    callback(response);
}

