/**
 * 
 * @param {Object} params 
 * @param {String} params.type    1token 3
 * @param {String} params.objectId id
 * @param {String[]} params.accessKeyList ,type1id3id(Freedo.createGuid())
 * @param {String} params.effectiveTime 
 * @param {String} params.expireTime 
 * @param {String} baseURL 
 * @param {String} token token
 * @param {FdServer.FdShareServer~ResponseCallback} response 
 * @private
 */
FdShareServer.addShareScene = async function(params, baseURL, token, callback) {
    let url = `${baseURL}/cloudlink/cloudlink-core-framework/app/dataresource/share/add?token=${token}`;
    let response = [];
    let data = {
        'shareType': params.shareType,
        'dataresourceId': params.objectId,
        'accessKeyList': params.accessKeyList,
        'status': 1,
        'dataType': params.dataType,
        'effectiveTime': params.effectiveTime,
        'expireTime': params.expireTime,
        'remark': params.remark
    };
    try {
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.log(error);
    }
    callback(response);
}

/**
 * 
 * @param {Object} params 
 * @param {Object} params.lid idpidlid
 * @param {Object} params.name name
 * @param {Object} params.shareType   1token 3
 * @param {String[]} params.accessKeyList accesskeyFreedo.createGuid()accesskey [Freedo.createGuid()]
 * @param {Object} params.effectiveTime 
 * @param {Object} params.expireTime 
 * @param {Object} params.dataType 
 * @param {String} baseURL 
 * @param {String} token 
 * @param {FdServer.FdShareServer~ResponseCallback} response 
 */
FdShareServer.share = async function(params, baseURL, token, callback) {
    FdShareServer.getShareObjectID({
        id: params.lid,
        name: params.name
    }, baseURL, token, res => {
        let objectId = undefined;
        if (res.rows.length) {
            objectId = res.rows[0].objectId;
            FdShareServer.addShareScene({
                shareType: params.shareType,
                objectId: objectId,
                accessKeyList: params.accessKeyList,
                effectiveTime: params.effectiveTime,
                expireTime: params.expireTime,
                dataType: params.dataType,
                remark: params.remark
            }, baseURL, token, res => {
                callback(res);
            })
        } else {
            // objectId
            Freedo.FdServer.FdShareServer.addShareObjectID({
                id: params.lid,
                name: params.name
            }, baseURL, token, res => {
                objectId = res.rows[0].objectId;
                FdShareServer.addShareScene({
                    shareType: params.shareType,
                    objectId: objectId,
                    accessKeyList: params.accessKeyList,
                    effectiveTime: params.effectiveTime,
                    expireTime: params.expireTime,
                    dataType: params.dataType,
                    remark: params.remark
                }, baseURL, token, res => {
                    callback(res);
                })
            })
        }
    })
}

