/**
 * accesskey
 * @param {String} accesskeyURL accesskeyurl
 * @param {String} dataType PMTSjsonxml
 * @param {FdServer.FdShareServer~ResponseCallback} response 
 * 
 * @example
 * let accesskeyURL = 'accesskeyurl';
 * let dataType = 'xml' || 'json';
 * Freedo.FdServer.FdShareServer.getLayerAccesskeyResource(accesskeyURL, dataType, res=> {});
 */
FdShareServer.getLayerAccesskeyResource = async function(accesskeyURL, dataType, callback) {
    try {
        let response = await FdCommon.ajaxJson({
            url: accesskeyURL,
            type: 'GET',
            dataType: dataType
        })
        callback(response);
    } catch (error) {
        callback(undefined, error);
    }
}

