import { FdCommon } from './FdCommon';

/**
 * 
 * @namespace
 * @alias FdServer.FdUserServer
 */
class FdUserServer {

}

/**
 * 
 * @param {String} telephone 
 * @param {String} baseURL 
 */
FdUserServer.getVerificationCode = async function(telephone, baseURL) {
    let data = {
        'sendMode': '1',
        'sendNum': telephone.toString(),
        'signName': '',
        'useCategory': 'regist'
    }
    try {
        let url = `${baseURL}/cloudlink/cloudlink-core-framework/verfy/getVerifyCode`;
        let response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.error(error);
    }
}

/**
 * 
 * @param {Object} data 
 * @param {String} data.telnumber 
 * @param {String} data.password 
 * @param {Number} data.verifycode 
 * @param {Number} [data.username] 
 * @param {FdServer.FdUserServer~ResponseCallback} response 
 */
FdUserServer.registerUser = async function(data, baseURL, callback) {
    let response = [];
    try {
        let url = baseURL + '/cloudlink/freeserver-admin/auth/regist';
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.error(error);
    }
    callback(response);
}

/**
 * 
 * @param {Object} data 
 * @param {String} data.mobileNum 
 * @param {String} data.password 
 * @param {Number} data.verifyCode 
 * @param {FdServer.FdUserServer~ResponseCallback} response 
 */
FdUserServer.changePassword = async function(data, baseURL, callback) {
    let response = [];
    try {
        let url = baseURL + '/cloudlink/cloudlink-core-framework/login/resetPassword';
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.error(error);
    }
    callback(response);
}

/**
 * 
 * @param {Object} data 
 * @param {String} data.loginNum 
 * @param {String} data.password 
 * @param {FdServer.FdUserServer~ResponseCallback} callback 
 */
FdUserServer.userLogin = async function(data, baseURL, callback) {
    let response = [];
    try {
        data.appId = '0154981b-11c5-49ad-839d-a3420af507d8';
        let url = `${baseURL}/cloudlink/cloudlink-core-framework/login/login`;
        response = await FdCommon.ajaxJson({
            url: url,
            data: data,
            type: 'POST'
        })
    } catch (error) {
        console.error(error);
    }
    callback(response);
}

/**
 * FdUserServer
 * @callback FdServer.FdUserServer~ResponseCallback
 * @param {Object} response 
 */

export {
    FdUserServer
}