import {
    FdMisc
} from '../../FdMisc/FdMisc';

/*
 * 
 * @private
 * @alias FdAnalysisPoint
 * @class
 */
class FdAnalysisPoint {
    constructor(viewer) {
        this._viewer = viewer;

        this._tempPoint = undefined;
        this._points = [];
        this._data = [];
        this._labels = [];
        this._bInSurvey = false;

        this._event = new FreeDo.Event();
        this._eventHelper = new FreeDo.EventHelper();
        this._tmpPoint = undefined;
        
        this._singleMode = false;
    }
}

/*
 * 
 * @param {Cartesian3} position 
 */
FdAnalysisPoint.prototype.addPoint = function(position) {
    var posC = Freedo.FdMisc.FdGeometric.tc(position);
    if (!Freedo.defined(this._viewer.scene.globe))
        posC = {
            x: position[0],
            y: position[1],
            z: position[2]
        };
    if (this._tmpPoint) {
        //this._viewer.entities.remove(this._tmpPoint);
    }
    this._tmpPoint = this._viewer.entities.add({
        position: posC,
        point: {
            color: Freedo.Color.YELLOW,
            pixelSize: 5,
            //
            disableDepthTestDistance: Number.POSITIVE_INFINITY,
            show: true
        }
    });

    var round = Freedo.FdMisc.FdMath.round
    var labelEntity;
    if (this._viewer.scene.globe)
        labelEntity = this.createLabel(posC, '' + round(position[0], 8) + '\n' + '' + round(position[1], 8) + '\n' + '' + round(position[2], 4));
    else
        labelEntity = this.createLabel(posC, 'X' + round(position[0], 8) + '\n' + 'Y' + round(position[1], 8) + '\n' + 'Z' + round(position[2], 4));
    this._labels.push(labelEntity);
    this._points.push(this._tmpPoint);

    var data = {};
    data.pointG = [round(position[0], 8), round(position[1], 8), round(position[2], 8)];
    data.pointC = posC;
    this._data.push(data);

    this._event.raiseEvent('Point', {
        point: position
    });

    this._event.raiseEvent('DataChanged', data);
    this._viewer.scene.requestRender();
}

/*
 * 
 * @param {Object} windowPosition 
 * @returns {Array} [,,]
 */
FdAnalysisPoint.prototype.pickPosition = function(windowPosition) {
    var position = Freedo.FdMisc.FdGeometric.pickPositionG(this._viewer, windowPosition);
    if (position) {
        this.addPoint(position);
    }
}

/**
 * Label
 * @private
 * @param {Object} pos 
 * @param {String} text 
 * @returns 
 */
FdAnalysisPoint.prototype.createLabel = function(pos, text) {
    return this._viewer.entities.add({
        position: pos,
        label: {
            style: Freedo.LabelStyle.FILL_AND_OUTLINE,
            text: text,
            show: true,
            showBackground: true,
            backgroundColor: new Freedo.Color(0.165, 0.165, 0.165, 1),
            font: '16px ',
            horizontalOrigin: Freedo.HorizontalOrigin.LEFT,
            verticalOrigin: Freedo.VerticalOrigin.BOTTOM,
            pixelOffset: new Freedo.Cartesian2(1, 1),
            disableDepthTestDistance: Number.POSITIVE_INFINITY
        }
    });
}

/**
 * 
 * @param {FdAnalysisPoint~FdAnalysisPointCallback} listener 
 * @param {Object} [scope] listener
 * @returns {Freedo.Event~RemoveCallback} 
 */
FdAnalysisPoint.prototype.on = function(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }
    /**
     * @callback FdAnalysisPoint~FdAnalysisPointCallback
     * @param {String} eventType Point
     * @param {Object} eventArg Point
     */

FdAnalysisPoint.prototype.handleLeftClick = function(movement) {
    if (this._bInSurvey == false)
        return;
    if(this._singleMode){
        this.removeAll();
    }
    this.pickPosition(movement.position);
};

FdAnalysisPoint.prototype.setSingleMode = function(singleMode) {
    this._singleMode = singleMode;
};

/**
 * 
 */
FdAnalysisPoint.prototype.getData = function() {
    return {
        point: Freedo.clone(this._data, true)
    }
}

/**
 * 
 */
FdAnalysisPoint.prototype.setData = function(v) {
    for (var k in v) {
        this.addPoint(v[k].pointG);
    }
}

/**
 * 
 */
FdAnalysisPoint.prototype.run = function(type) {
    this._hanlder = new Freedo.ScreenSpaceEventHandler(this._viewer.scene.canvas);
    this._hanlder.setInputAction(this.handleLeftClick.bind(this), Freedo.ScreenSpaceEventType.LEFT_CLICK);
    this._bInSurvey = true;
}

/**
 * 
 */
FdAnalysisPoint.prototype.reset = function() {
    this._bInSurvey = false;
    this._hanlder.removeInputAction(Freedo.ScreenSpaceEventType.LEFT_CLICK);
};

/**
 * 
 */
FdAnalysisPoint.prototype.removeAll = function() {
    for (var k in this._points) {
        this._viewer.entities.remove(this._points[k]);
    }
    for (var k in this._labels) {
        this._viewer.entities.remove(this._labels[k]);
    }
    this._points = [];
    this._data = [];
};

/**
 * 
 */
FdAnalysisPoint.prototype.dispose = function() {
    this.reset();
    this.removeAll();
};

export {
    FdAnalysisPoint
};