import { FdPolylineEditor } from './Analysis/FdPolylineEditor.js';
import { FdEraseGroundManager } from '../FdTools/FdEraseGroundManager.js';

/**
 * 
 *
 * @alias FdMicroApp.FdEraseGroundHandler
 * @class
 * @param {Freedo.Viewer} viewer 
 * @param {FdTools.FdEraseGroundManager} [eraseGroundManager] ,
 * 
 *
 * @example
 * var container = document.getElementById('freedoContainer');
 * 
 * var earseGroudManager = new Freedo.FdTools.FdEraseGroundManager(viewer);
 * 
 * var earseGroudHandler = new Freedo.FdMicroApp.FdEraseGroundHandler(viewer,earseGroudManager);
 * 
 * // click a button to start draw dig hole polygon(convex);
 * earseGroudHandler.start();
 * 
 */
class FdEraseGroundHandler {

    constructor(viewer, eraseGroundManager) {
        this._viewer = viewer;

        this._lsner = undefined;

        if (!Freedo.defined(eraseGroundManager)) {
            eraseGroundManager = new FdTools.FdEraseGroundManager(viewer);
        }
        this._eraseGroundManager = eraseGroundManager;

        this._ple = new FdPolylineEditor(viewer);
        this._ple.setAutoDelLines(true);

        this._data = [];

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        var that = this;
        this._eraseGroundManager.on(function(eventType, param) {
            if (eventType === 'dataAdded') {
                that._data.push(param);
                that._event.raiseEvent('dataChanged', param);
            }
        });
    }

    /**
     * 
     * @type {FdTools.FdEraseGroundManager}
     */
    get eraseGoundManager() {
        return this._eraseGroundManager;
    };

    /**
     * 
     * @returns {Object[]} eraseObjectArray 
     * @param {Freedo.Cartesian3[]} eraseObjectArray[i].points 
     * @param {Number[]} eraseObjectArray[i].color , [157 / 255, 226 / 255, 53 / 255]
     * @param {Freedo.GroundErasePrimitive} eraseObjectArray[i].erasePrimitive 
     * 
     */
    getData() {
        return this._data;
    }

    /**
     * 
     * 
     * @param {Number[]} [color]  [157 / 255, 226 / 255, 53 / 255]
     * 
     */
    start(color) {
        color = Freedo.defaultValue(color, [0.0, 0.0, 0.0, 1.0]);
        this._ple.run();

        if (this._lsner) {
            this._lsner();
            this._lsner = undefined;
        }

        var that = this;

        this._lsner = this._ple.on(function(eventType, param) {
            if (eventType === 'PLAdd') {
                that.onPLAdd(param, color);
            }
        });

        this._event.raiseEvent('start', {});
    }

    /**
     * @private
     */
    onPLAdd(param, color) {
        if (!param.polyline)
            return;

        if (param.polyline.pts.length === 2) {
            this._ple.delResult();
            return;
        }

        this._eraseGroundManager.eraseGroudByPointsArray(param.polyline.pts, color);
    }

    /**
     * 
     * 
     */
    cancel() {
        this._ple.cancel();

        this._event.raiseEvent('cancel', {});
    }

    /**
     * start,cancel,dispose,dataChanged
     * @param {FdMicroApp.FdEraseGroundHandler~FdEraseGroundChangedCallback} listener 
     * @param {Object} [scope] listener
     * @returns {Freedo.Event~RemoveCallback} 
     * 
     */
    on(listener, scope) {
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdMicroApp.FdEraseGroundHandler~FdEraseGroundChangedCallback
     * @param {String} eventType startcanceldisposedataChanged
     * @param {Object} eventArg dataChanged{@link FdMicroApp.FdEraseGroundHandler#getData};start/cancel/dispose
     */

    /**
     * @private
     */
    reset() {
        if (this._lsner)
            this._lsner();
        this._lsner = undefined;

        this._data = [];
        this._ple.cancel();

        this._event.raiseEvent('Dispose', {});
    }

    /**
     * 
     * 
     */
    dispose() {
        this.reset();
        this._eventHelper.removeAll();
    }
}

export { FdEraseGroundHandler }