/**
 * 
 *
 * @alias FdTools.FdImageryLayersManager
 * @param {Freedo.Viewer} viewer 
 * @class
 *
 * @example
 * var container = document.getElementById('freedoContainer');
 * 
 * var imageryManager = new Freedo.FdTools.FdImageryLayersManager(viewer);
 * 
 * imageryManager.addDefaultImageryLayer();
 */
class FdImageryLayersManager {

    constructor(viewer) {
        this._imageryLayersOpts = [];

        this._viewer = viewer;
        this._imageryLayers = viewer.imageryLayers;

        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._imageryLayers.layerAdded.addEventListener(FdImageryLayersManager.prototype._rawEventCallback, this);
        this._imageryLayers.layerMoved.addEventListener(FdImageryLayersManager.prototype._rawEventCallback, this);
        this._imageryLayers.layerRemoved.addEventListener(FdImageryLayersManager.prototype._rawEventCallback, this);
        this._imageryLayers.layerShownOrHidden.addEventListener(FdImageryLayersManager.prototype._rawEventCallback, this);
    }

    /**
     * {@link FdTools.FdImageryLayersManager#add}
     * @type {Object[]}
     * 
     * @example
     * 
     * var imageryLayersOptions = imageryManager.imageryLayersOptions;
     * for(var i = 0 ;i < imageryLayersOptions.length ;i++){
     *      var imageryLayersOption = imageryLayersOptions[i] ;
     *      var iconUrl = imageryLayersOption.iconUrl; //url
     *      var name = imageryLayersOption.name; //
     *      var type = imageryLayersOption.type; //BingMaps/Mapbox/ESRI/OpenStreetMap/WMTS/TileMapService/UrlTemplate
     *      var layerOption = imageryLayersOption.layerOption; //alpha/showalphashow
     *      var alpha = layerOption.alpha; //[0.0, 1.0]
     *      var show = layerOption.show; //
     *      var providerOptions = imageryLayersOption.providerOptions; //imageryLayerOptions.typetypeBingMapsproviderOptions.urlproviderOptions.mapStyletypeESRIproviderOptions.urlproviderOptions.enablePickFeatures
     * }
     * 
     * 
     */
    get imageryLayersOptions() {
        return this._imageryLayersOpts;
    };

    /**
     * 
     * @type {Number}
     */
    get length() {
        return this._imageryLayersOpts.length;
    };

    /**
     * @private
     */
    _rawEventCallback() {
        this._event.raiseEvent('Changed');
    }

    /**
     * 
     */
    addDefaultImageryLayer() {
        // var imageryLayerOptions = {
        //     'name': '',
        //     'type': 'WMTS',
        //     'iconUrl': '../../Freedo/Freedo/Widgets/Images/ImageryProviders/blackMarble.png',
        //     'layerOption': {
        //         'show': true,
        //         'alpha': 1.0
        //     },
        //     'providerOptions': {
        //         'url': '//219.142.143.106:7090/rest/wmts/',
        //         'layer': '',
        //         'style': 'default',
        //         'format': 'tiles',
        //         'tileMatrixSetID': '',
        //         'minimumLevel': 0,
        //         'maximumLevel': 18,
        //         'tilingScheme': 'Geographic'
        //     }
        // };

        var imageryLayerOptions = {
            'name': '',
            'type': 'UrlTemplate',
            //'iconUrl': '//res.gbim360.com/lib/freedo/rel/1.5.8/Freedo/Widgets/Images/ImageryProviders/blackMarble.png',
            'iconUrl': '../../Freedo/Freedo/Widgets/Images/ImageryProviders/blackMarble.png',
            'layerOption': {
                'show': true,
                'alpha': 1.0
            },
            'providerOptions': {
                'url': '//mt0.google.cn/vt/lyrs=s&hl=zh-CN&x={x}&y={y}&z={z}',
                'maximumLevel': 20,
                'tilingScheme': 'WebMercator'
            }
        }

        var index = this._imageryLayers.length;
        this.add(imageryLayerOptions, index);
    }

    /**
     * 
     * @param {Number} index 
     * @param {String} property alpha/showalphashow
     * @returns {Number/Boolean} alphaNumbershowBoolean
     */
    getLayerProperty(index, property) {
        var imageLayerOpt = this._imageryLayersOpts[index];

        if (typeof(imageLayerOpt) === 'undefined') {
            throw new Error('FdImageryLayersManager.getLayerProperty: cannot get property!');
        }

        if (imageLayerOpt.layerOption && typeof(imageLayerOpt.layerOption[property]) !== 'undefined') {
            //return Freedo.clone(imageLayerOpt.layerOption[property], true);
            return imageLayerOpt.layerOption[property];
        } else {
            var layer = this._imageryLayers.get(index);
            if (typeof(layer[property] !== 'undefined')) {
                return layer[property];
            } else {
                throw new Error('FdImageryLayersManager.getLayerProperty: cannot get property!');
            }
        }
    }

    /**
     * Changed
     * @param {Number} index 
     * @param {String} property alpha/showalphashow
     * @param {Number/Boolean} value alphaNumbershowBoolean
     */
    setLayerProperty(index, property, value) {
        // 0 sceneObject
        var imageLayerOpt = this._imageryLayersOpts[index];
        if (typeof(imageLayerOpt) === 'undefined') {
            throw new Error('FdImageryLayersManager.setLayerProperty: cannot get property!');
        }

        var oldValue = this.getLayerProperty(index, property);

        if (_.isEqual(oldValue, value)) {
            return;
        }

        // 1 
        var layer = this._imageryLayers.get(index);
        if (typeof(layer[property] !== 'undefined')) {
            layer[property] = value;
        } else {
            throw new Error('FdImageryLayersManager.setLayerProperty: cannot get property!');
        }

        // 2 option
        if (typeof imageLayerOpt.layerOption === 'undefined') {
            imageLayerOpt.layerOption = {};
        }
        imageLayerOpt.layerOption[property] = value;

        // 3 
        this._event.raiseEvent('Changed');

        this._viewer.scene.requestRender();
    }

    /**
     * 
     * @param {Object} imageryLayerOptions 
     * @param {String} imageryLayerOptions.iconUrl url
     * @param {String} imageryLayerOptions.name 
     * @param {String} imageryLayerOptions.type BingMaps/Mapbox/ESRI/OpenStreetMap/WMTS/TileMapService/UrlTemplate
     * @param {Object} imageryLayerOptions.layerOption alpha/showalphashow
     * @param {Number} imageryLayerOptions.layerOption.alpha [0.0, 1.0]
     * @param {Boolean} imageryLayerOptions.layerOption.show 
     * @param {Object} imageryLayerOptions.providerOptions imageryLayerOptions.typetypeBingMapsproviderOptions.urlproviderOptions.mapStyletypeESRIproviderOptions.urlproviderOptions.enablePickFeatures
     * @param {Number} [index] 
     * 
     * @example
     * 
     * var imageryLayerOptions = {
     *     'name': '',
     *     'type': 'WMTS',
     *     'iconUrl': '../../Freedo/Freedo/Widgets/Images/ImageryProviders/blackMarble.png',
     *     'layerOption': {
     *         'show': true,
     *         'alpha': 1.0
     *     },
     *     'providerOptions': {
     *         'url': '//219.142.143.106:7090/rest/wmts/',
     *         'layer': '',
     *         'style': 'default',
     *         'format': 'tiles',
     *         'tileMatrixSetID': '',
     *         'minimumLevel': 0,
     *         'maximumLevel': 18,
     *         'tilingScheme': 'Geographic'
     *     }
     * };
     * 
     * var index = imageLayersManager.length;
     * imageLayersManager.add(imageryLayerOptions, index);
     * 
     */
    add(imageryLayerOptions, index) {

        //
        if (this._imageryLayersOpts.length === 0) {
            this._imageryLayers.removeAll();
            index = 0;
        }

        if (typeof index === 'undefined') {
            index = this._imageryLayersOpts.length;
            this._imageryLayersOpts.push(imageryLayerOptions);
        } else {
            this._imageryLayersOpts.splice(index, 0, imageryLayerOptions);
        }
        var providerOptions = Freedo.clone(imageryLayerOptions.providerOptions, true);

        var imageProvider;
        switch (imageryLayerOptions.type) {
            case 'BingMaps':
                {
                    if (providerOptions.mapStyle === 'AERIAL') {
                        providerOptions.mapStyle = Freedo.BingMapsStyle.AERIAL;
                    } else if (providerOptions.mapStyle === 'AERIAL_WITH_LABELS') {
                        providerOptions.mapStyle = Freedo.BingMapsStyle.AERIAL_WITH_LABELS;
                    } else if (providerOptions.mapStyle === 'ROAD') {
                        providerOptions.mapStyle = Freedo.BingMapsStyle.ROAD;
                    }
                    imageProvider = new Freedo.BingMapsImageryProvider(providerOptions);
                    break;
                }
            case 'Mapbox':
                {
                    imageProvider = new Freedo.MapboxImageryProvider(providerOptions);
                    break;
                }
            case 'ESRI':
                {
                    imageProvider = new Freedo.ArcGisMapServerImageryProvider(providerOptions);
                    break;
                }
            case 'OpenStreetMap':
                {
                    imageProvider = Freedo.createOpenStreetMapImageryProvider(providerOptions);
                    break;
                }
            case 'WMTS':
                {
                    if (providerOptions.tilingScheme === 'Geographic') {
                        providerOptions.tilingScheme = new Freedo.GeographicTilingScheme();
                    }

                    if (providerOptions.tileDiscardPolicy) {
                        var jsonOpts = providerOptions.tileDiscardPolicy;
                        if (jsonOpts.pixelsToCheck) {
                            var ptjson = providerOptions.tileDiscardPolicy.pixelsToCheck;
                            var pixelsToCheck = [];
                            for (var i = 0; i < ptjson.length; ++i) {
                                pixelsToCheck.push(new Freedo.Cartesian2(ptjson[i][0], ptjson[i][1]));
                            }
                            jsonOpts.pixelsToCheck = pixelsToCheck;
                        }
                        providerOptions.tileDiscardPolicy = new Freedo.DiscardMissingTileImagePolicy(jsonOpts);
                    }
                    imageProvider = new Freedo.WebMapTileServiceImageryProvider(providerOptions);
                    break;
                }
            case 'TileMapService':
                {
                    if (providerOptions.tilingScheme === 'WebMercator') {
                        providerOptions.tilingScheme = new Freedo.WebMercatorTilingScheme();
                    } else if (providerOptions.tilingScheme === 'Geographic') {
                        providerOptions.tilingScheme = new Freedo.GeographicTilingScheme();
                    }
                    imageProvider = Freedo.createTileMapServiceImageryProvider(providerOptions);
                    break;
                }
            case 'UrlTemplate':
                {
                    if (providerOptions.tilingScheme === 'WebMercator') {
                        providerOptions.tilingScheme = new Freedo.WebMercatorTilingScheme();
                    } else if (providerOptions.tilingScheme === 'Geographic') {
                        providerOptions.tilingScheme = new Freedo.GeographicTilingScheme();
                    }
                    imageProvider = new Freedo.UrlTemplateImageryProvider(providerOptions);
                    break;
                }
        }

        this._imageryLayers.addImageryProvider(imageProvider, index);

        var layer = this._imageryLayers.get(index);
        for (var property in imageryLayerOptions.layerOption) {
            this.setLayerProperty(index, property, imageryLayerOptions.layerOption[property]);

            if (typeof(layer[property] !== 'undefined')) {
                layer[property] = imageryLayerOptions.layerOption[property];
            } else {
                throw new Error('FdImageryLayersManager.setLayerProperty: cannot get property!');
            }
        }
    }

    /**
     * 
     * @param {Number} index 
     * 
     * @example
     * 
     * //[a,b,c,d]
     * imageLayersManager.raise(2) ;
     * //[a,c,b,d]
     *
     */
    raise(index) {
        if (index >= 0 && index < this.length - 1) {
            var temp = this._imageryLayersOpts[index];
            this._imageryLayersOpts[index] = this._imageryLayersOpts[index + 1];
            this._imageryLayersOpts[index + 1] = temp;

            var layer = this._imageryLayers.get(index);
            this._imageryLayers.raise(layer);
            this._viewer.scene.requestRender();
        }
    }


    /**
     * 
     * @param {Number} index 
     * 
     * @example
     * //[a,b,c,d]
     * imageLayersManager.lower(1) ;
     * //[a,c,b,d]
     * 
     * 
     */
    lower(index) {
        if (index > 0 && index < this.length) {
            var temp = this._imageryLayersOpts[index];
            this._imageryLayersOpts[index] = this._imageryLayersOpts[index - 1];
            this._imageryLayersOpts[index - 1] = temp;

            var layer = this._imageryLayers.get(index);
            this._imageryLayers.lower(layer);
            this._viewer.scene.requestRender();
        }
    }

    /**
     * 
     * @param {Number} index 
     * 
     * @example
     * //[a,b,c,d]
     * imageLayersManager.remove(2) ;
     * //[a,b,d]
     * 
     */
    remove(index) {
        if (index >= 0 && index < this.length) {
            this._imageryLayersOpts.splice(index, 1);
            var layer = this._imageryLayers.get(index);
            this._imageryLayers.remove(layer);
        }
    }

    /**
     * 
     * 
     * @example
     * //[a,b,c,d]
     * imageLayersManager.removeAll() ;
     * //[]
     * 
     */
    removeAll() {
        this._imageryLayersOpts.splice(0, this.length);
        this._imageryLayers.removeAll();
    }

    /**
     * 
     */
    getAllLayers() {
        return Freedo.clone(this._imageryLayersOpts, true); // 
    }


    /**
     * FdImageryLayersManager
     * @param {FdTools.FdImageryLayersManager~FdImageryLayersManagerCallback} listener FdImageryLayersManagerChanged
     * @param {Object} [scope] listener
     * @returns {Freedo.Event~RemoveCallback} 
     * 
     * @example
     * 
     * imageLayersManager.on( function(eventType ,param){
     *      if(eventType === 'Changed'){
     * 
     *      }
     * });
     * 
     */
    on(listener, scope) {
        // return this._event.addEventListener(listener, scope);
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdTools.FdImageryLayersManager~FdImageryLayersManagerCallback
     * @param {String} eventType Changed
     * 
     */

    /**
     * 
     * @private
     */
    reset() {
        this.removeAll();
    }

    /**
     * 
     * 
     * @example
     * 
     * imageLayersManager.dispose();
     */
    dispose() {
        this.reset();
        this._eventHelper.removeAll();
    }
}

export { FdImageryLayersManager }