import { flyToByCameraInfo, goToByCameraInfo } from '../../FdCamera/moveByCameraInfo.js';
import { FdFlyToController } from './FdFlyToController.js';

var scratchTransformMatrix = new Freedo.Matrix4();


/**
 * 
 *
 * @alias FdTools.FdViewPointsManager
 * @param {Freedo.Viewer} viewer 
 * @class
 *
 * @example
 * var viewer = Freedo.FdApp.createDefaultViewer('freedoContainer');
 * 
 * var viewPointsManager = new Freedo.FdTools.FdViewPointsManager(viewer);
 * 
 * var currentGroupId = viewPointsManager.createGroup('Group_' + new Date().getTime());
 * var cameraInfo = Freedo.FdCamera.getCurrentCameraInfo(scene.camera);
 * 
 * viewPointsManager.appendViewpoint(currentGroupId, {
 *     name: '_' + new Date().getTime(),
 *     icon: imgData,
 *     cameraInfo: cameraInfo
 * });
 * 
 */
class FdViewPointsManager {

    constructor(viewer) {
        this._viewpointOpts = {};

        this._viewer = viewer;
        this._event = new Freedo.Event();
        this._eventHelper = new Freedo.EventHelper();

        this._flytoControl = new FdFlyToController(viewer.scene.camera);
        this._currentPlayingGroupID = undefined;

        var that = this;
        this._flytoControl.on(function(eventType, param) {
            that.flyToEventCallback(eventType, param)
        });
    }

    /**
     * ,{@link FdTools.FdViewPointsManager#getGroupOptions},{@link FdTools.FdViewPointsManager#appendViewpoint}
     * @type {Object}
     * 
     * @example
     * var viewPointsOptions = viewPointsManager.viewPointsOptions ;
     * for(var key in viewPointsOptions){
     *      var groupId = key ;
     *      var groupOption = viewPointsOptions[groupId];
     *      var name = groupOption.name;
     *      var viewPointsList = groupOption.list;
     *      for(var i = 0 ;i <viewPointsList.length ;i++){
     *          var viewPointOption = viewPointsList[i] ;
     *          var viewPointName = viewPointOption.name ;
     *          var cameraInfo = viewPointOption.cameraInfo ;
     *      }
     * }
     * 
     */
    get viewPointsOptions() {
        return this._viewpointOpts;
    }

    /**
     * 
     * @type {Boolean}
     */
    get playingLoop() {
        return this._flytoControl.loop;
    }

    /**
     * 
     * @type {Boolean}
     */
    set playingLoop(value) {
        if (this._flytoControl.loop !== value) {
            this._flytoControl.loop = value;
        }
    }

    /**
     * @private
     */
    flyToEventCallback(eventType, param) {
        if (eventType === 'CurrentIndexChanged') {
            this._event.raiseEvent('PlayingGroupCurrentIndexChanged', param);
        } else if (eventType === 'StatusChanged') {
            this._event.raiseEvent('PlayingGroupStatusChanged', param);
        } else if (eventType === 'PropertyChanged') {
            this._event.raiseEvent('PlayingGroupPropertyChanged', param);
        }
    }

    /**
     * 
     * @param {String} groupName 
     * @param {String} [groupID] Id,GuidId
     * @returns {String} ID
     * 
     * @example
     * 
     * var currentGroupId = viewPointsManager.createGroup( 'Group_' + new Date().getTime() );
     * 
     */
    createGroup(groupName, groupID) {
        if (typeof groupID === 'undefined') {
            groupID = Freedo.createGuid();
        }

        this._viewpointOpts[groupID] = {
            name: groupName,
            list: [],
        };

        this._event.raiseEvent('GroupsChanged', groupID);
        return groupID;
    }

    /**
     * 
     * @param {String} groupID ID
     * @returns {Boolean} truefalse
     * 
     * @example
     * 
     * viewPointsManager.removeGroup( currentGroupId );
     * 
     */
    removeGroup(groupID) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return false;
        }

        if (this._currentPlayingGroupID === groupID) {
            this._flytoControl.reset();
        }

        delete this._viewpointOpts[groupID];
        this._event.raiseEvent('GroupsChanged', groupID);

        return true;
    }

    /**
     * 
     * @param {String} groupID ID
     * @returns {String} :''
     * 
     * @example
     * 
     * var groupName = viewPointsManager.getGroupName( currentGroupId ); 
     * 
     */
    getGroupName(groupID) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return '';
        }

        return this._viewpointOpts[groupID].name;
    }

    /**
     * 
     * @param {String} groupID ID
     * @param {String} groupName 
     * 
     * @example
     * 
     * viewPointsManager.setGroupName( currentGroupId  ,'NewGroupName'); 
     * 
     */
    setGroupName(groupID, groupName) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return '';
        }

        this._viewpointOpts[groupID].name = groupName;
    }

    /**
     * 
     * @param {String} groupID ID
     * @returns {Object} 
     * @param {String} options.name 
     * @param {Object[]} options.list 
     * @example
     * 
     * var groupOptions = viewPointsManager.getGroupOptions( currentGroupId  ); 
     * 
     */
    getGroupOptions(groupID) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return undefined;
        }

        var groupOptions = Freedo.clone(this._viewpointOpts[groupID], true);
        return groupOptions;
    }

    /**
     * 
     * 
     * @example
     * 
     * viewPointsManager.removeAllGroups(   ); 
     * 
     */
    removeAllGroups() {
        this._flytoControl.reset();
        for (var groupID in this._viewpointOpts) {
            delete this._viewpointOpts[groupID];
        }
        this._currentPlayingGroupID = undefined;

        this._event.raiseEvent('GroupsChanged');

        return true;
    }

    /**
     * ID
     * @returns {String[]} ID
     * 
     * @example
     * var groupIdsArray = viewPointsManager.getAllGroupIDs();
     * 
     */
    getAllGroupIDs() {
        var allGroupIDs = [];
        for (var groupID in this._viewpointOpts) {
            allGroupIDs.push(groupID);
        }

        return allGroupIDs;
    }

    /**
     * 
     * @param {String} groupID ID
     * @param {Object} options 
     * @param {String} options.name 
     * @param {Number[]} options.cameraInfo {@link FdCamera.getCurrentCameraInfo}
     * @returns {Boolean} truefalse
     * 
     * @example
     * 
     * var currentGroupId;
     * currentGroupId = viewPointsManager.createGroup('Group_' + new Date().getTime());
     * 
     * var cameraInfo = Freedo.FdCamera.getCurrentCameraInfo(scene.camera);
     * 
     * viewPointsManager.appendViewpoint(currentGroupId, {
     *     name: '_' + new Date().getTime(),
     *     cameraInfo: cameraInfo
     * });
     * 
     */
    appendViewpoint(groupID, options) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return false;
        }

        var viewpointOpt = Freedo.clone(options, true);
        this._viewpointOpts[groupID].list.push(viewpointOpt);
        this._event.raiseEvent('GroupChanged', groupID);

        return true;
    }

    /**
     * 
     * @param {String} groupID ID
     * @param {Object} options 
     * @param {String} options.name 
     * @param {Number[]} options.cameraInfo {@link FdCamera.getCurrentCameraInfo}
     * @param {Number} index 
     * @returns {Boolean} truefalse
     * 
     * @example
     * 
     * var currentGroupId;
     * currentGroupId = viewPointsManager.createGroup('Group_' + new Date().getTime());
     * 
     * var cameraInfo = Freedo.FdCamera.getCurrentCameraInfo(scene.camera);
     * viewPointsManager.insertViewpoint(currentGroupId, {
     *     name: '_' + new Date().getTime(),
     *     cameraInfo: cameraInfo
     * } ,0 );
     * 
     */
    insertViewpoint(groupID, options, index) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return false;
        }

        var viewpointOpt = Freedo.clone(options, true);
        this._viewpointOpts[groupID].list.splice(index, 0, viewpointOpt);
        this._event.raiseEvent('GroupChanged', groupID);

        return true;
    }

    /**
     * 
     * @param {String} groupID ID
     * @param {Number} index 
     * 
     * @example
     * 
     * viewPointsManager.removeViewpoint(currentGroupId,0); 
     * 
     */
    removeViewpoint(groupID, index) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return false;
        }

        if (typeof(this._viewpointOpts[groupID].list[index]) === 'undefined') {
            return false;
        }

        this._viewpointOpts[groupID].list.splice(index, 1);
        this._event.raiseEvent('GroupChanged', groupID);

        return true;
    }

    /**
     * 
     * @param {String} groupID ID
     * @returns {Number} 
     * @example
     * 
     * var viewPointsLengthInGroup = viewPointsManager.getViewpointsLength(currentGroupId,0); 
     * 
     */
    getViewpointsLength(groupID) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            throw new Error('getViewpointsLength error!');
        }

        return this._viewpointOpts[groupID].list.length;
    }

    /**
     * 
     * @param {String} groupID ID
     * @param {Number} index 
     * @param {Object} options cameraInfo
     * @param {String} options.name 
     * @param {Number[]} options.cameraInfo {@link FdCamera.getCurrentCameraInfo}
     * @returns {Boolean} truefalse
     * 
     * @example
     * var cameraInfo = Freedo.FdCamera.getCurrentCameraInfo(scene.camera);
     * viewPointsManager.setViewpoint(currentGroupId, 0 ,{
     *     name: '_' + new Date().getTime(),
     *     cameraInfo: cameraInfo
     * } );
     * 
     */
    setViewpoint(groupID, index, options) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return false;
        }

        if (typeof(this._viewpointOpts[groupID].list[index]) === 'undefined') {
            return false;
        }

        var viewpointOpt = Freedo.clone(options, true);

        this._viewpointOpts[groupID].list[index] = viewpointOpt;
        this._event.raiseEvent('ViewPointChanged', viewpointOpt);

        return true;
    }

    /**
     * 
     * @param {String} groupID ID
     * @param {Number} index 
     * @returns {Object} options cameraInfo
     * @param {String} options.name 
     * @param {Number[]} options.cameraInfo {@link FdCamera.getCurrentCameraInfo}
     * 
     * 
     * @example
     * 
     * var viewPointName = viewPointsOptions.name ;
     * 
     * var cameraInfo = viewPointsOptions.cameraInfo;
     * 
     */
    getViewPoint(groupID, index) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            throw new Error('getViewPoint error');
        }

        if (typeof(this._viewpointOpts[groupID].list[index]) === 'undefined') {
            return new Error('getViewPoint error');
        }

        var options = this._viewpointOpts[groupID].list[index];
        var viewpointOpt = Freedo.clone(options, true);
        return viewpointOpt;
    }

    /**
     * 
     * @param {String} groupID ID
     * @param {Number} index 
     * @returns {Boolean} truefalse
     * 
     * @example
     * 
     * viewPointsManager.flyTo(currentGroupId ,0 ) ;
     * 
     */
    flyTo(groupID, index) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return false;
        }

        if (typeof(this._viewpointOpts[groupID].list[index]) === 'undefined') {
            return false;
        }

        var options = this._viewpointOpts[groupID].list[index];
        var cam = options.cameraInfo;
        if (typeof cam === 'undefined') {
            return false;
        }
        var camera = this._viewer.scene.camera;
        flyToByCameraInfo(camera, cam);

        return true;
    }

    /**
     * 
     * @param {String} groupID ID
     * @param {Number} index 
     * @returns {Boolean} truefalse
     * 
     * @example
     * 
     * viewPointsManager.goTo(currentGroupId ,0 ) ;
     * 
     */
    goTo(groupID, index) {
        if (typeof(this._viewpointOpts[groupID]) === 'undefined') {
            return false;
        }

        if (typeof(this._viewpointOpts[groupID].list[index]) === 'undefined') {
            return false;
        }

        var options = this._viewpointOpts[groupID].list[index];
        var cam = options.cameraInfo;
        if (typeof cam === 'undefined') {
            return false;
        }

        var camera = this._viewer.scene.camera;
        goToByCameraInfo(camera, cam);

        return true;
    }

    /**
     * 
     * @param {String} groupID ID
     * 
     * 
     * @example
     * 
     * viewPointsManager.startOrResumeGroup(currentGroupId, 0);
     * 
     */
    startOrResumeGroup(groupID) {
        if (typeof groupID === 'undefined') {
            if (typeof this._currentPlayingGroupID !== 'undefined') {
                groupID = this._currentPlayingGroupID;
            } else {
                throw new Error('FdViewPointsManager startOrResume should have argument!');
            }
        }

        if (typeof this._currentPlayingGroupID !== 'undefined') {
            if (groupID !== this._currentPlayingGroupID) {
                this._flytoControl.stop();
                this._flytoControl._setViewpoints(null);

                this._flytoControl._setViewpoints(this._viewpointOpts[groupID].list);
                this._currentPlayingGroupID = groupID;

                this._flytoControl.startOrResume();
            } else {
                this._flytoControl.startOrResume();
            }
        } else {
            this._flytoControl._setViewpoints(this._viewpointOpts[groupID].list);
            this._currentPlayingGroupID = groupID;

            this._flytoControl.startOrResume();
        }
    }

    /**
     * 
     * 
     * @example
     * 
     * viewPointsManager.pauseGroup();
     * 
     */
    pauseGroup() {
        this._flytoControl.pause();
    }

    /**
     * 
     * 
     * @example
     * 
     * viewPointsManager.stopGroup();
     * 
     */
    stopGroup() {
        this._flytoControl.stop();
    }

    /**
     * 
     * @returns {String}  Playing/Stopped/Paused
     * 
     * @example
     * 
     * var isPlaying = viewPointsManager.getGroupPlayingStatus();
     * 
     */
    getGroupPlayingStatus() {
        return this._flytoControl.status;
    }

    /**
     * 
     * @returns {Number} 
     * 
     * 
     * @example
     * 
     * var playingIndex = viewPointsManager.getGroupPlayingIndex();
     * 
     */
    getGroupPlayingIndex() {
        return this._flytoControl.currentIndex;
    }

    /**
     * 
     * @param {Number} index 
     * 
     * @example
     * 
     * viewPointsManager.setGroupPlayingIndex(0);
     * 
     */
    setGroupPlayingIndex(index) {
        this._flytoControl.currentIndex = index;
    }

    /**
     * ID
     * @returns {String} ID
     * 
     * @example
     * 
     * var playingGroupId = viewPointsManager.getGroupPlayingGroupID();
     * 
     */
    getGroupPlayingGroupID() {
        return this._currentPlayingGroupID;
    }

    /**
     * FdViewpointsManager
     * PlayingGroupCurrentIndexChanged/PlayingGroupStatusChanged/PlayingGroupPropertyChanged,GroupsChanged,ViewPointChanged
     * @param {FdTools.FdViewpointsManager~FdViewpointsManagerCallback} listener 
     * @param {Object} [scope] listener
     * @returns {Freedo.Event~RemoveCallback} 
     * 
     * @example
     * 
     * viewPointsManager.on( function(eventType, param){
     *  if (eventType === 'CurrentIndexChanged') {
     *      //do something
     *  } else if (eventType === 'StatusChanged') {
     *     //do something
     *  } else if (eventType === 'PropertyChanged') {
     *     //do something
     *  }
     * })
     * 
     */
    on(listener, scope) {
        // return this._event.addEventListener(listener, scope);
        return this._eventHelper.add(this._event, listener, scope);
    }

    /**
     * @callback FdTools.FdViewpointsManager~FdViewpointsManagerCallback
     * @param {String} eventType PlayingGroupCurrentIndexChanged/PlayingGroupStatusChanged/PlayingGroupPropertyChanged,GroupsChanged,ViewPointChanged
     * @param {String | Number | Boolean | Object} eventArg PlayingGroupCurrentIndexChangedIndexnumber;PlayingGroupStatusChangedStringPlaying,Paused,Stopped;GroupsChangedid;ViewPointChanged{@link FdTools.FdViewPointsManager#setViewpoint}
     */

    /**
     * 
     * @private
     * 
     */
    reset() {
        this.removeAllGroups();
        this._flytoControl.reset();
    }

    /**
     * 
     * 
     * @example
     * viewPointsManager.dispose() ;
     */
    dispose() {
        this.reset();
        this._flytoControl.dispose();
        this._eventHelper.removeAll();
    }
}

export {
    FdViewPointsManager
};